<template>
  <div style="min-width: 180px">
    <multiselect
      v-model="selectedRecords"
      :object="true"
      :label="label"
      :track-by="tracker"
      :placeholder="placeholder"
      open-direction="bottom"
      :options="[
        { value: 'batman', label: 'Batman' },
        { value: 'robin', label: 'Robin', disabled: true },
        { value: 'joker', label: 'Joker' },
      ]"
      :searchable="true"
      :loading="isLoading"
      :internal-search="true"
      :clear-on-select="true"
      :min-chars="3"
      :limit="300"
      :show-no-results="false"
      :hide-selected="false"
      @search-change="handleSearch"
      @select="$emit('changed', $event)"
    >
    </multiselect>
  </div>
</template>
<script>
import Multiselect from "@vueform/multiselect";
export default {
  name: "TypeHead",
  props: {
    url: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
    tracker: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    loadDefaults: {
      type: Boolean,
      default: false,
      required: false,
    },
  },
  components: {
    Multiselect,
  },
  data: () => ({
    selectedRecords: {},
    records: [],
    isLoading: false,
  }),
  created() {
    if (this.loadDefaults) {
      this.getDefaultData();
    }
  },
  methods: {
    getDefaultData(url = null) {
      let fetchUrl = url || this.url;
      this.$store.commit("SET_REQUEST_FLAG", "AUTOCOMPLETE_REQUEST");
      this.$store.dispatch("getRequest", fetchUrl).then((response) => {
        if (response.data.rows) this.records = response.data.rows;
        else this.records = response.data || [];
        this.isLoading = false;
      });
    },
    limitText(count) {
      return `and ${count} other records`;
    },
    handleSearch(query) {
      if (query.trim().length > 0) {
        this.isLoading = true;
        this.$store.commit("SET_REQUEST_FLAG", "AUTOCOMPLETE_REQUEST");
        this.$store
          .dispatch("getRequest", `${this.url}?query=${query}`)
          .then((response) => {
            if (response.data.rows) this.records = response.data.rows;
            else this.records = response.data || [];
            this.isLoading = false;
          });
      }
    },
    handleChange(event) {
      console.log(event);
      this.$emit("changed", event);
    },
    clearAll() {
      this.selectedRecords = {};
      this.records = [];
    },
  },
  // mounted() {
  //   this.clearAll();
  // },
};
</script>
<style src="@vueform/multiselect/themes/default.css"></style>
